* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
}
html {
  scroll-behavior: smooth;
}
:root{
  --bac-font: Browallia New;
  --font-size:11px;
  --bold-font:Aharoni;
  --menu-width: 8vw;
  --menu-4th-column-width: 160px;
  --menu-4th-5th-column-width: 200px;
}

.menu-icon, .menu-black-icon {
  -webkit-mask-image: url(/static/media/menu.b8b0c41f.svg) !important;
          mask-image: url(/static/media/menu.b8b0c41f.svg) !important;
  -webkit-mask-size: 27px 27px;
          mask-size: 27px 27px;
  -webkit-mask-repeat: no-repeat;
          mask-repeat: no-repeat;
  -webkit-mask-position: center;
          mask-position: center;
  width: 27px;
  height: 27px;
}



.home-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 45px 141px 0 50px;
}
.header-logo-text {
  font-family: Source Serif Pro;
  font-weight: 600;
  font-size: 50px;
  line-height: 63px;
