  cursor: pointer;
}

.close-icon {
  width: 17px;
  height: 17px;
  -webkit-mask-image: url(/static/media/close-button.cd8a2343.svg);
          mask-image: url(/static/media/close-button.cd8a2343.svg);
  -webkit-mask-repeat: no-repeat;
          mask-repeat: no-repeat;
  -webkit-mask-position: center;
          mask-position: center;
  -webkit-mask-size: cover;
          mask-size: cover;
  background-color: black;
}

.menu-icon {
  background-color: white !important;
  background-size: contain;
  background-repeat: no-repeat;
  width: 30px;
  height: 42px;
}
.awssld {
  z-index: -10;
  --loader-bar-height: 0px;
}
.awssld__timer,
.awssld__timer--run {
  opacity: 0;
  height: 0 !important;
}
.awssld__bullets {
  z-index: 10;
  bottom: 35.5px;
}
.awssld__bullets button {
  width: 14px;
  height: 14px;
  background: rgba(203, 203, 203, 0.59);
  -webkit-transform: scale(1);
          transform: scale(1);
}
.awssld__bullets--active {
  background-color: #fff !important;
}
.awssld__bullets .awssld__bullets--active {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.menu {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: #fff;
  display: grid;
  grid-template-rows: auto 1fr;
  z-index: 11;
  -webkit-animation-name: menu-anim;
          animation-name: menu-anim;
}
@-webkit-keyframes menu-anim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes menu-anim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 35px 93px 0 50px;
}
.menu-logo h3 {
  font-family: Source Serif Pro;
  font-style: normal;
  font-weight: 600;
  font-size: 50px;
  line-height: 63px;
  letter-spacing: 0.12em;
  color: #434b62;
}
.menu-pageheader {
  font-family: var(--bold-font);
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
}
.menu-page dt {
  margin-bottom: 10px;
}
.pages a {
  font-family: var(--bac-font);
  font-weight: normal;
  line-height: 28px;
  color: #000;
}
.menu-page:not(:first-child) {
  margin-left: 4vw;
  width: 10vw;
}
.menu-container {
  margin: auto;
  display: grid;
  grid-template-rows: repeat(2, -webkit-max-content);
  grid-template-rows: repeat(2, max-content);
  align-items: center;
  max-height: -webkit-fill-available;
  overflow: auto;
}
.menu-columns {
  width: 100%;
}
.menu-item-titles {
  display: grid;
  grid-template-columns: 3.2vw 8vw 8vw 9vw 8vw;
  grid-column-gap: 6.7vw;
  align-items: flex-end;
  margin-bottom: 10px;
}

.menu-items {
  display: grid;
  grid-template-columns: 3.2vw 8vw 8vw 9vw 8vw;
  grid-column-gap: 6.7vw;
}

@media screen and (max-width: 1620px) {
