    grid-template-columns: 5.2vw 10.8vw 10.9vw 12.4vw 10.7vw;
  }
  .menu-items {
    grid-template-columns: 5.2vw 10.8vw 10.9vw 12.4vw 10.7vw;
  }
}

@media screen and (max-width: 1200px) {
